<template>
  <div class="wc">
    <div class="right">
      <ul class="flwz">目录</ul>
      <ul class="lb" v-html="right_data"></ul>
    </div>
    <div class="left">
      <div class="article">
        <ul class="dh">{{getOne}} / {{getTwo}}</ul>
        <div class="head">
          <h1>{{title}}</h1>
          <span>发布日期:</span>
          <span class="rq">{{time}}</span>
          <span>&nbsp;&nbsp;&nbsp;&nbsp;分类：{{getOne}}</span>
          <ul>
            分享到:
            <span @click="share('wx')" title="微信"></span>
            <span @click="share('wb')" title="微博"></span>
            <span @click="share('qq好友')" title="qq好友"></span>
          </ul>
        </div>
        <div class="body" v-html="html"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { mapActions } from "vuex";
import { marked } from "../marked/marked";

export default {
  name: "HelloWorld",
  data() {
    return {
      html: "",
      title: "",
      time: "",
      right: {}, //右侧导航
      getOne: "",
      getTwo: "",
      dj: 1, //右侧导航是几级目录
      right_data: ""
    };
  },
  beforeRouteEnter: (to, from, next) => {
    var data = () => import(`../data/${to.query.getTwo}.js`);
    // import(`../data/${this.json[this.$route.query.getOne][this.$route.query.getTwo]["content"]}`);
    data().then(a => {
      next(function(vm) {
        vm.$parent.wait = "";
        var data = a.func(marked);
        vm.title = data.title;
        vm.html = data.comtent;
        vm.time = data.time;
        vm.right_data = vm.jgh(data.structure);
      });
    });
  },
  methods: {
    share(a) {
      var url = location.href;

      console.log(a);
      if (a == "qq好友") {
        var p = {
          url:
            "https://cht111.github.io/blog/#/article?getOne=%E5%85%B3%E4%BA%8E%E5%89%8D%E7%AB%AF&getTwo=vuex" /*获取URL，可加上来自分享到QQ标识，方便统计*/,
          // desc:"来自汽车氪的分享" /*分享理由(风格应模拟用户对话),支持多分享语随机展现（使用|分隔）*/,
          title: this.title /*分享标题(可选)*/,
          // summary: title /*分享描述(可选)*/,
          // pics: pic /*分享图片(可选)*/,
          // flash: "" /*视频地址(可选)*/,
          //commonClient : true, /*客户端嵌入标志*/
          site: "CHT111" /*分享来源 (可选) ，如：QQ分享*/
        };

        var s = [];
        for (var i in p) {
          s.push(i + "=" + encodeURIComponent(p[i] || ""));
        }
        var target_url =
          "http://connect.qq.com/widget/shareqq/iframe_index.html?" +
          s.join("&");
        window.open(target_url, "qq", "height=520, width=720");
      }
      if (a == "wx") {
        var target_url = "http://qr.liantu.com/api.php?text=" + url;
        window.open(target_url, "weixin", "height=320, width=320");
      }
      if (a == "wb") {
        var param = {
          url: url,
          type: "3",
          count: "1" /** 是否显示分享数，1显示(可选)*/,
          // appkey: "汽车氪" /** 您申请的应用appkey,显示分享来源(可选)*/,
          title: this.title /** 分享的文字内容(可选，默认为所在页面的title)*/,
          // pic: pic,
          /**分享图片的路径(可选)*/ ralateUid:
            "" /**关联用户的UID，分享微博会@该用户(可选)*/,
          rnd: new Date().valueOf()
        };
        var temp = [];
        for (var p in param) {
          temp.push(p + "=" + encodeURIComponent(param[p] || ""));
        }
        var target_url =
          "http://service.weibo.com/share/share.php?" + temp.join("&");
        window.open(target_url, "sinaweibo", "height=430, width=400");
      }
    },
    addID(data) {
      for (var i = 0; i < data.length; i++) {
        data[i].id = data[i].innerHTML;
      }
    },
    //结构化右侧目录
    jgh(data) {
      // console.log(data);
      var stru = "";
      for (var i in data) {
        stru += `<jg${this.dj}>${i}</jg${this.dj}>`;
        if (typeof data[i] != "object") {
          this.dj -= data[i];
        } else {
          this.dj++;
          stru += this.jgh(data[i]);
        }
      }
      return stru;
    }
    // xr() {
    //   var data = () =>
    //     import(// import(`../data/${this.json[this.$route.query.getOne][this.$route.query.getTwo]["content"]}`);
    //     `../data/${this.$route.query.getTwo}.js`);
    //   data().then(a => {
    //     var data = a.func(marked);
    //     this.title = data.title;
    //     this.html = data.comtent;
    //     this.time = data.time;
    //     this.right_data = this.jgh(data.structure);
    //   });
    // }
  },
  computed: {},
  created() {
    this.getOne = this.$route.query.getOne;
    this.getTwo = this.$route.query.getTwo;
    var that = this;
    // 放在路由守卫渲染，在获取数据以后再进入当前页面
    // this.xr();
    setTimeout(() => {
      var that = this;
      document.getElementsByClassName("lb")[0].onclick = function(e) {
        that.addID(document.getElementsByTagName("bt4"));
        that.addID(document.getElementsByTagName("bt2"));
        that.addID(document.getElementsByTagName("bt3"));
        var top = document.getElementById(e.target.innerHTML).offsetTop - 60;
        var id1 = setInterval(() => {
          var Y1 = window.scrollY + 80;
          window.screen.height;
          document.body.scrollHeight;

          if (Y1 >= top) {
            Y1 = top;
            clearInterval(id1);
          } else {
          }
          window.scrollTo(0, Y1);
          if (Y1 + window.screen.height >= document.body.scrollHeight) {
            window.scrollTo(
              0,
              document.body.scrollHeight - window.screen.height + 1000
            );
            clearInterval(id1);
          }
        }, 3);
      };
    }, 1);
  }
};
</script>

<style scoped lang='less'>
@media screen and (max-width: 720px) {
  .wc {
    display: block;
  }
  .left,
  .right {
    width: 100%;
  }
  .right {
    height: auto;
    padding: 10px 0;
    .lb {
      overflow: hidden !important;
    }
  }
}
@media screen and (min-width: 720px) {
  .wc {
    display: flex;
  }
  .left {
    width: 70%;
  }
  .right {
    width: 27%;
    height: 450px;
  }
}
.wc {
  // display: flex;
  transition: all 1s;
  // overflow: hidden;

  // height: 20px;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: flex-start;
  .left {
    // width: 70%;
    padding-bottom: 20px;
    box-sizing: border-box;
    .article {
      .head {
        margin: 0 0 20px 0;
        span {
          color: #686868;
          line-height: 2;
        }
        h1 {
          line-height: 2;
        }
        ul {
          display: flex;
          align-items: center;
          color: #686868;
          span {
            height: 23px;
            width: 23px;
            border-radius: 50%;
            margin: 0 5px;
            cursor: pointer;
          }
          span:nth-child(1) {
            display: inline-block;
            background-image: url(../assets/mlogo.png);
          }
          span:nth-child(2) {
            display: inline-block;
            background-image: url(../assets/mlogo.png);
            background-position: 48px 0;
          }
          span:nth-child(3) {
            display: inline-block;
            background-image: url(../assets/mlogo.png);
            background-position: 23px 0;
          }
        }
      }
      .dh {
        color: #8d9095;
        margin-bottom: 30px;
      }
    }
  }
  .right {
    // width: 27%;

    box-sizing: border-box;
    padding: 10px;

    // border: 1px solid red;
    background: #f5f5f5;
    border-radius: 10px;
    .flwz {
      font-size: 20px;
      font-weight: bolder;
      margin: 0 0 10px 0;
    }
    .lb {
      box-sizing: border-box;
      height: 90%;
      overflow-y: scroll;
    }
  }
}
</style>



