import marked from 'marked'
import hljs from '../../static/js/heighlight'
let renderMD = new marked.Renderer();
marked.setOptions({
    renderer: renderMD,
    gfm: true,
    tables: true,
    breaks: false,
    pedantic: false,
    sanitize: false,
    smartLists: true,
    smartypants: false,
    highlight: function (code) {
        return hljs.highlightAuto(code).value;
    }
})
hljs.initHighlightingOnLoad();
export { marked }



