<template>
  <div class="index">
    <div v-for="(item,index) in indexData" :key="index">
      <ul @click="toArticle(index)" class="title">{{item['title']}}</ul>
      <ul class="date">发布日期：{{item['time']}}</ul>
      <ul class="introduce">{{item['introduce']}}</ul>
    </div>
  </div>
</template>

<script>
// import { mapGetters, mapActions } from "vuex";
import { Default } from "../json/json";
export default {
  data() {
    return {
      indexData: ""
    };
  },
  methods: {
    // ...mapActions(["setOne", "setTwo"]),
    toArticle(a) {
      // this.setTwo(a);
      sessionStorage.getTwo = a;
      this.$parent.wait = "wait";
      this.$router.push({
        path: "/article",
        query: {
          getOne: sessionStorage.getOne,
          getTwo: sessionStorage.getTwo
        }
      });
    }
  },
  computed: {
    // ...mapGetters(["getOne", "getTwo"])
  },
  created() {
    this.indexData = this.$route.query.fenlei
      ? this.json[this.$route.query.fenlei]
      : Default;
  },
  watch: {
    $route(a) {
      console.log(a.query.fenlei);
      this.indexData = this.json[a.query.fenlei];
    }
  }
};
</script>

<style scoped lang='less'>
@media screen and (max-width: 720px) {
  .index {
    justify-content: center;
    div {
      width: 90%;
    }
  }
}
@media screen and (min-width: 720px) {
  .index {
    justify-content: space-between;
    div {
      width: 49%;
    }
  }
}
.index {
  display: flex;
  flex-wrap: wrap;

  div {
    border: 1px solid #a3a3a3;
    border-radius: 5px;
    // width: 49%;
    padding: 20px;
    box-sizing: border-box;
    margin: 20px 0;
    .title {
      font-size: 20px;
      cursor: pointer;
      color: #9ec09f;
    }
    .date {
      color: #686868;
      font-size: 12px;
      line-height: 2;
    }
    .introduce {
      font-size: 14px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  div:hover {
    .title {
      color: red;
    }
  }
}
</style>



